import React, { Component } from 'react';
import Link from 'next/link';


class SignupButton extends Component {



  render () {
    return(
      <button style={{ }}>
        <Link href="/tickets">
          <a style={{backgroundColor:"#FAC55B", textDecoration: "none", color: "#fff"}}>snag a ticket</a>
        </Link>
      </button>
    );
  };

};

export default SignupButton;



